@import "../../../../../index.scss";
@import "../../../../../utils/scss/constants.scss";

.wrapper__modal_window {
    position: absolute;
    top: -4rem;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background: rgba(215, 210, 210, 0.262);
    z-index: 2;
    min-height: 100%;
    padding: 6rem 2rem 2rem 2rem;

    .modal_window {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 3rem;
        padding: 1.8rem;
        width: 100%;
        max-width: 470px;
        left: 0;
        right: 0;
        border-radius: .3rem;
        background-color: var(--black-color);

        .modal_window__title {
            text-align: center;
            font-weight: 500;
            font-size: 2rem;
            line-height: 1.2;
        }

        .wrapper__input_board_title,
        .wrapper__input_board_description {
            height: 3rem;
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }

        .wrapper__submit_button {
            height: 3rem;
        }
    }
}